<template>
  <div>
    <div v-if="pagination.total > 0" class="pagination">
      <div class="display-page">
        <h3 class="mr-3">{{ $t("components.pagination.display") }}</h3>
        <select name="" id="itemsPerPage" v-model="selectedItemsPerPage">
          <option v-for="option in [5, 10]" :value="option" :key="option">
            {{ option }}
          </option>
        </select>
      </div>
      <div v-if="pagination.total > pagination.per_page" class="pagination-arrow">
        <button
          :disabled="pagination.current_page == 1"
          class="page-item-navigation"
          @click="changePage(1)"
        >
          <i class="icon-arrow-p-left"></i>
        </button>
        <button
          :disabled="pagination.current_page == 1"
          class="page-item-navigation"
          @click="changePage(pagination.current_page - 1)"
        >
          <i class="icon-arrow-tab-left"></i>
        </button>
        <div v-for="index in pagination.last_page" :key="index">
          <button
            v-if="index >= pagesRange.start && index <= pagesRange.end"
            @click="changePage(index)"
            :class="{ active: index === pagination.current_page }"
          >
            {{ index }}
          </button>
        </div>
        <button
          :disabled="pagination.current_page == pagination.last_page"
          @click="changePage(pagination.current_page + 1)"
          class="page-item-navigation"
        >
          <i class="icon-arrow-tab-right"></i>
        </button>
        <button
          :disabled="pagination.current_page == pagination.last_page"
          class="page-item-navigation"
          @click="changePage(pagination.last_page)"
        >
          <i class="icon-arrow-p-right"></i>
        </button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    storeModuleName: String,
    storeFunctionName: String,
    getPaginationName: String,
    setPaginationName: String,
    idParam: Number,
  },
  data() {
    return {
      selectedItemsPerPage: 5,
    };
  },
  methods: {
    changePage(value) {
      let paginationSet = "setPagination";
      let paginationget = "getPagination";

      if (
        this.setPaginationName &&
        this.setPaginationName != undefined &&
        this.setPaginationName !== ""
      ) {
        paginationSet = this.setPaginationName;
      }
      if (
        this.getPaginationName &&
        this.getPaginationName != undefined &&
        this.getPaginationName !== ""
      ) {
        paginationget = this.getPaginationName;
      }
      this.$store.dispatch(`${this.storeModuleName}/${paginationSet}`, {
        ...this.$store.getters[`${this.storeModuleName}/${paginationget}`],
        ["current_page"]: value,
      });
      if (!this.storeFunctionName) {
        this.$store.dispatch(`${this.storeModuleName}/getData`);
      } else {
        this.$store.dispatch(
          `${this.storeModuleName}/${this.storeFunctionName}`,
          this.idParam&&this.idParam
        );
      }
    },
  },
  watch: {
    selectedItemsPerPage(value) {
      let paginationSet = "setPagination";
      let paginationget = "getPagination";
      if (
        this.setPaginationName &&
        this.setPaginationName != undefined &&
        this.setPaginationName !== ""
      ) {
      
        paginationSet = this.setPaginationName;
      }
      if (
        this.getPaginationName &&
        this.getPaginationName != undefined &&
        this.getPaginationName !== ""
      ) {
        paginationget = this.getPaginationName;
      }
      this.$store.dispatch(`${this.storeModuleName}/${paginationSet}`, {
        ...this.$store.getters[`${this.storeModuleName}/${paginationget}`],
        ["per_page"]: value,
        ["current_page"]: 1,
      });
      if (!this.storeFunctionName) {
        this.$store.dispatch(`${this.storeModuleName}/getData`);
      } else {
        this.$store.dispatch(`${this.storeModuleName}/${this.storeFunctionName}`,this.idParam);
      }
    },
  },
  computed: {
    pagination() {
      let paginationget = "getPagination";
      if (
        this.getPaginationName &&
        this.getPaginationName != undefined &&
        this.getPaginationName !== ""
      ) {
        paginationget = this.getPaginationName;
      }
      return this.$store.getters[`${this.storeModuleName}/${paginationget}`];
    },
    pagesRange() {
      let paginationget = "getPagination";
      if (
        this.getPaginationName &&
        this.getPaginationName != undefined &&
        this.getPaginationName !== ""
      ) {
        paginationget = this.getPaginationName;
      }
      let paginationData = this.$store.getters[
        `${this.storeModuleName}/${paginationget}`
      ];
      let pages = paginationData.last_page;
      let currentPage = paginationData.current_page;
      const rangeSize = 3; // Adjust this value to set the range size.
      let startPage = currentPage - Math.floor(rangeSize / 2);
      let endPage = currentPage + Math.floor(rangeSize / 2);
      if (startPage < 1) {
        startPage = 1;
        endPage = Math.min(rangeSize, pages);
      }
      if (endPage > pages) {
        endPage = pages;
        startPage = Math.max(1, endPage - rangeSize + 1);
      }
      return {
        start: startPage,
        end: endPage,
      };
    },
  },
};
</script>
